@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
.trackContent {
  padding: 10px 5px; 
  margin-bottom: 10px;
  border-bottom: 1px solid #f7f7f7;
}

.trackContent * {
  display: inline-block;
  padding: 0 5px;
}

.trackName p {
  display: block;
  text-align: left;
}

#playButton,
#saveButton {
  height: 28px;
  width: 28px;
}

#playButton:hover {
  cursor: pointer;
}
.mainContentPage {
  text-align: center;
  flex:4 1;
  padding: 15px;
}

#searchInput {
  border: 1px solid #bcbec2;
  height: 40px;
  max-width: 484px;
  width: auto;
  padding: 0 10px;
  border-radius: 14px;
  font-size: 16px;
  outline: none;
}

.trackCtn {
  max-width: 425px;
  margin: 10px auto;
  padding: 10px;
  text-align: left;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
}

.moremenu {
  display: none;
  position: absolute;
  
  min-width: 80px;
  z-index: 2;
  border: .4px solid rgb(230, 227, 227);
  bottom: -10px;
  box-shadow: 5px 8px 16px 5px rgba(0,0,0,0.2);
  left: -5px;
  top: 14px;
  
}

.moremenu ul {
  list-style-type: none;
  width: 80px;
  font-size: 10px;
  text-align: left;
  
}

.moremenu li {
  text-align: left;
  width: 80px;
  padding: 10px 5px;
  background-color: #ffffff;
}

.moremenu li:hover {
  background-color: rgb(211, 199, 182);
}

.dropdown:hover .moremenu {
  display: block;
}


.remove-btn:hover {
  cursor: pointer;
}
.playlist-item-ctn {
  display: flex;
  list-style-type: none;
  align-items: center;
  border-bottom: .5px solid #fff;
}

.playlist-item-ctn * {
  
  text-align: center;
  
}

.playlist-item-ctn img {
  width: 28px;
  height: auto;
}

.playlist-item-ctn img:hover {
  cursor: pointer;
}

.play-ctn,
#more-icon {
  width: 15%;
}

#more-icon img {
  width: 22px;
  height: auto;
}

.track-name,
.track-artist {
  width: 35%;
}

.dropdown {
  position: relative;
  display: inline-block;
}


.likedsongs {
  flex: 4 1;
  padding: 10px 5px;
}

.likedsongs ul {
  list-style-type: none;

}

@media only screen and (min-width: 600px) {
  .likedsongs {
    margin: 0 auto;
    width: 600px;
  }
}
.home {
  flex: 4 1;
  padding: 10px 5px;
}
.topbar {
  width: 100%;
  height: 65px;
  background-color: rgb(226, 224, 224);
  box-shadow: 0px 2px 10px rgb(0 0 0 / 15%);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 5;
}

.topbarWrapper {
  height: 100%;
  padding: 0 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 29px;
  font-weight: 800;
  cursor: pointer;
}

.topRight {
  display: flex;
  align-items: center;
}

.topbarIcon {
  margin: 0 5px;
  cursor: pointer; 
  color: #555;
}

.topAvatar {
  margin-left: 10px;
}
.sidebar {
  color: green;
  flex: 1 1;
  position: -webkit-sticky;
  position: sticky;
  top: 65px;
  height: calc(100vh - 65px);
  background-color: rgba(187, 186, 186, 0.2);
  box-shadow: 0px 2px 10px rgb(0 0 0 / 15%);
}

.sidebarWrapper {
  padding: 20px;
}

.sidebarTitle {
  font-size: 14px;
  color: rgb(148, 147, 147);
  padding: 10px 0;
}

.sidebarList {
  list-style-type: none;
  padding: 10px;
  
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  color: rgb(46, 45, 45);
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  border-radius: 10px;
  
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgba(158, 157, 157, 0.2);

}

.sidebarIcon {
  margin-right: 6px;
}


* {
  margin:0;
  padding: 0;
  font-family: 'Lato';
  box-sizing: border-box;
}

.container {
  display: flex;
}

a:visited {
  text-decoration: none;
}

.rLink,
.rLink:link,
.artistLink:link,
.artistLink,
.songLink {
  text-decoration: none;
  color: black;
  
}

.trackCtn * {
  background-color: #ffffff;
}

.videoplayer {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.videoplayerCloseBtn {
  cursor: pointer;
}

.sim-artist-ctn,
.sim-songs-ctn {
  flex:4 1;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
}
.loginBar {
  width: 100%;
  height: 65px;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgb(0 0 0 / 15%);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 5;
}

.loginBarWrapper {
  height: 100%;
  padding: 0 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 29px;
  font-weight: 800;
  cursor: pointer;
}

.topRight {
  display: flex;
  align-items: center;
}

.loginBarIcon {
  margin: 0 15px;
}

.loginBtn {
  color: white !important;
  font-size: 24px !important;
  text-transform: none !important;
  font-family: 'Lato'!important;
}

.demoBtn {
  color: white !important;
  font-size: 14px !important;
  font-family: 'Lato'!important;
}

